"use client";

import { useEvents } from "@/app/context/eventsContext";

export default function Filtros() {
  const { colunas } = useEvents();

  if (!colunas.length) return null;

  return (
    <select>
      <option>Filtrar por...</option>
      {colunas.map(col => (
        <option key={col}>{col}</option>
      ))}
    </select>
  );
}
