"use client";

import { useEvents } from "@/app/hooks/useEvents";

export default function TabelaEventos() {
  const { eventos, colunas } = useEvents();

  if (!eventos.length) return null;

  return (
    <table>
      <thead>
        <tr>
          {colunas.map(col => (
            <th key={col}>{col}</th>
          ))}
        </tr>
      </thead>
      <tbody>
        {eventos.map((ev, i) => (
          <tr key={i}>
            {colunas.map(col => (
              <td key={col}>{ev[col]}</td>
            ))}
          </tr>
        ))}
      </tbody>
    </table>
  );
}
